import items from "./items.vue";
import empty from "@/components/common/empty/empty.vue";
import { mapMutations, mapGetters } from 'vuex';
export default {
	data() {
		return {
			loadEnd: false, // 判断所有接口是否加载完成
			dataList: null // 后台返回数据
		};
	},
	components: {
		items,
		empty
	},
	computed: {
		...mapGetters(['SELECTTEMPLATERHX'])
	},
	watch: {
	},
	created() {
		console.log('在组件中并不能使用页面生命周期函数');
		this.getList();
	},
	mounted() {
	},
	methods: {
		...mapMutations({
			settotal: 'settotal' // 融头条参数
		}),
		getList(){
			let params = {}; // 请求总数居时 参数为空
			if (!uni.getStorageSync('landRegist')) {
				this.landRegistra(); // 判断登录状态
			} else if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				uni.showLoading({
					// 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/temp', //接口地址。
					data: params,
					method: 'GET',
					header: {
						Authorization: 'Bearer ' + landRegistLG.token //将token放到请求头中
					},
					success: response => {
						console.log(response.data, '---------------response.data--------------')
						if (response.data.ret === '200') {
							uni.hideLoading(); // 隐藏 loading
							this.dataList = response.data.content;
							this.loadEnd = true; // 加载完成
						} else {
							uni.hideLoading(); // 隐藏 loading
							uni.showToast({
								title: '网络开小差了，请稍后重试',
								icon: 'none',
								duration: 1000
							});
						}
					},
					fail: error => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		}
	}
};
